.headerAlt {
    display: block;
    top: 0;
    background: #fff;
    background-color:  #89c5c6;
    height: 85px;
    position: sticky;
    border: 1px solid #89c5c6;
    box-shadow: 0px 4px 4px 0px rgb(82, 2, 161, 0.25);
    z-index: 100;
}

.headerAlt__width {
    margin-right: auto;
    margin-left: auto;
    padding-top: 5px;
    padding-left: 5px;
    padding-right: 5px;
}

.headerAlt__up {
    display: flex;
    align-items: center;
    justify-content: space-between;
    
    
}

.headerAlt__down {
    display: flex;justify-content: center;
}

.headerAlt__left {
    display: flex;
}

.headerAlt__logo, .headerAlt__inProgress, .headerAlt__welcome {
    background-color: #fff;
    display: flex;
    box-shadow: 0px 4px 4px 0px rgb(82, 2, 161, 0.25);
    border-top-right-radius: 15px;
    padding: 5px;
    margin-right: 2px;
}


.headerAlt__logo > h1, .headerAlt__inProgress > h1, .headerAlt__welcome > h1 {
    font-size: 10px;
    align-self: center;
    flex-grow: 1;
}


.headerAlt__logo {
    padding: 2px;
}

.headerAlt__center {
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
}

.headerAlt__avatar {
    background-color: pink;
    height: 40px;
    width: 40px;
    border-radius: 50px;
}


.middleColor__bar {
    display: flex;
}

.top__star {
    position: absolute;
    top: 0;
    right: 60%;
}

@media screen and (min-width: 901px) {
    .headerAlt {
        display: none;
    }
}

@media screen and (max-width: 292px) {
    .smallest {
        display: none;
    }
}